	export default {
		data() {
			return {
				judgeyzm : false,
				judgestart : false,
				judgeimg : true,
				sjh : this.user.tel||'未登录',
				yzm : "",
				imgurl : "",
				inst:null,
				s:60,
				imgArr:null,
				upData:null,
				uid:this.user.uid
			}
		},
		onLoad() {
		    this.in1();
			this.login()
		},
		methods: {
			back : function(){
			   uni.navigateBack({
			  
			   });
			},
			login:function(){
				let that=this;
				let usurl=that.url1+"/dinghai/shop/byid";
				uni.showLoading({
					title:"正在查询中",
					mask:true
				})
				uni.request({
						url: usurl,
						header:{
							"Content-Type" : "application/x-www-form-urlencoded"
						},
						method:'GET',
						data: {
							uid:that.uid
						},
				    success: (res) => {
						if(res.statusCode==200){
							console.log(res)
							if(res.data.msg=="success"){
								uni.showToast({
									title:"已入驻",
									icon:"none"
								})
								uni.redirectTo({
									url:"shangpin/shangpin"
								})
							}else{
								uni.showToast({
									title:"未入驻请入驻",
									icon:"none"
								})
								
							}
						}else{
							uni.showToast({
								title:"您的网络打了个盹！",
								icon:"none"
							})
						}
				    },
					fail : function(err){
						uni.showToast({
							title:"您的网络打了个盹",
							icon:"none"
						})
						uni.navigateBack({
							
						})
					}
				});
			},
			start : function(){
				if(this.judgestart == false){
					uni.showToast({
						title : "请填写完整的店铺信息(包括店铺头像)",
						icon : "none"
					})
				}else{
						let that=this;
						let usurl2=that.url1+"/dinghai/user/smslogin";
						uni.showLoading({
							title:"正在验证",
							mask: true
						})
						uni.request({
								url: usurl2,
								header:{
									"Content-Type" : "application/x-www-form-urlencoded"
								},
								method:'POST',
								data: {
									tel:that.sjh,
									sms:that.yzm
								},
						    success: (res) => {
								if(res.statusCode==200){
									console.log(res)
									if(res.data.msg=="success"){
										uni.navigateTo({
											url:"info/info?image="+that.upData
										})
									}
								}else{
									uni.showToast({
										title:"您的网络打了个盹",
										icon:"none"
									})
								}
						    },
							fail : function(err){
								uni.showToast({
									title:"您的网络打了个盹",
									icon:"none"
								})
							}
						});
				}
			},
			in1:function(){
				this.sjh=='未登录'?this.judgeyzm = false:this.judgeyzm = true;
			},
			
			in2 : function(){
				if(this.sjh != "" && this.yzm != "" &&this.judgeimg == false){
					this.judgestart = true;
				}else{
					this.judgestart = false;
				}
			},
			
			   // 获取验证码
			getcode : function(){
					if(this.judgeyzm){
						let that=this;
						let usurl=that.url1+"/dinghai/user/sms";
						uni.showLoading({
							title:"正在发送",
							mask: true
						})
						uni.request({
								url: usurl,
								header:{
									"Content-Type" : "application/x-www-form-urlencoded"
								},
								method:'POST',
								data: {
									tel:that.sjh
								},
						    success: (res) => {
								if(res.statusCode==200){
									uni.showToast({
										icon:"none",
										title:"发送成功请接受"
									})
								}else{
									uni.showToast({
										title:"您的网络打了个盹",
										icon:"none"
									})
								}
						    },
							fail : function(err){
								uni.showToast({
									title:"您的网络打了个盹",
									icon:"none"
								})
							}
						});
					}
					
					
					this.judgeyzm = false;
					this.inst=setInterval(()=>{
						if(this.s<=0){
							this.s=60;
							clearInterval(this.inst)
						}else{
							this.s--
						}
					},1000)
				
			},
			   // 选择图片
			sel_img : function(){
				var that = this;
				
					
				
					uni.chooseImage({
					   count: 1, //默认9
					   sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
					   sourceType: ['album'], //从相册选择
					   success: function (res) {
						   console.log(res.tempFilePaths,res);
						   let thatRes=res;
						   if(/(.jpg|.JPG|.jpeg|.JPEG|.PNG|.png)$/.exec(res.tempFilePaths[0])){
							   let uploadTask=uni.uploadFile({
							   url: that.url1+'/dinghai/Img/Img', //仅为示例，非真实的接口地址
							   filePath:thatRes.tempFilePaths[0],
							   name: 'files',
							   fileType:"image",
							   success: (uploadFileRes) => {
							       if(uploadFileRes.statusCode===200){
							   		that.imgArr=thatRes.tempFilePaths[0]
							   		that.upData=uploadFileRes.data
									that.judgeimg=false
							   		console.log(that.upData)
							   		
							   	}else{
							   		uni.showToast({
							   			title: '上传图片失败请重试',
							   			icon:"none"
							   		});
							   	}
							   }
							   });
						   }else{
							   uni.showToast({
							   	icon:"none",
								title:"上传格式错误！请上传jpg，jpeg，png格式的图片"
							   })
						   }
					   }
					});
				
			}
			
		 }
}
